<template>
	<v-form ref="addTform">
		<v-container>
			<v-layout row wrap>
				<v-flex xs12>
				<v-layout row wrap>
					<h4 class="display-1">  </h4>
					<v-tooltip bottom v-model="showTestTooltip">
				        <v-btn slot="activator" @click="showTestTooltip = !showTestTooltip" icon small> <v-icon color="light-blue darken-1">info</v-icon></v-btn>
				        <span>    .          ,        . </span>
					</v-tooltip>
				</v-layout>
			</v-flex>

				<v-flex xs12 sm6>
					<v-label> </v-label>
					<v-tooltip bottom v-model="showTitleTooltip">
				        <v-btn slot="activator" @click="showTitleTooltip = !showTitleTooltip" icon small> <v-icon color="light-blue darken-1">info</v-icon></v-btn>
				        <span>, '  '</span>
		        	</v-tooltip>
		            <v-text-field
		              required
              		  :rules="[rules.required, rules.title]"
              		  v-model="title"
              		  solo
		            ></v-text-field>
		        </v-flex>

  				<v-flex xs12 sm6>
  					<v-label>  </v-label>
					<v-tooltip bottom v-model="showLinkTooltip">
				        <v-btn slot="activator" @click="showLinkTooltip = !showLinkTooltip" icon small> <v-icon color="light-blue darken-1">info</v-icon></v-btn>
				        <span>            .           ,    .</span>
		        	</v-tooltip>
		            <v-text-field
		              placeholder="your-test"
		              required
            		  prefix="/"
            		  solo
              		  v-model="token"
              		  :hint="validToken ? '' : '   !   .'"
              		  :rules="[rules.tokenValid, rules.token]"
              		  :append-icon="validToken ? 'done' : 'block'"
              		  :error="!validToken"
		            ></v-text-field>
		        </v-flex>

				<v-flex xs12>
					<v-label> : {{questionsChecks.length}}</v-label>
					<div class="content-wrapper">
						<added-question
						:collection="questions"
						:withchecks="1"
						:questionsChecks="questionsChecks"
						@update:questionsChecks="questionsChecks = $event"
						@update:collection="toggleShow($event)"
						></added-question>
						<v-label v-if="error">    .    .</v-label>
					</div>
				</v-flex>

				<v-flex xs12>
  					<v-label></v-label>
					<v-tooltip bottom v-model="showDescriptionTooltip">
				        <v-btn slot="activator" @click="showDescriptionTooltip = !showDescriptionTooltip" icon small> <v-icon color="light-blue darken-1">info</v-icon></v-btn>
				        <span>    . ,     ,    ,         ..</span>
		        	</v-tooltip>
			        <v-textarea
			            v-model="description"
          		  		:rules="[rules.required, rules.description]"
			            required
              			solo
			          ></v-textarea>
		        </v-flex>

		        <v-flex xs12 sm6>
  					<v-label> </v-label>
					<v-tooltip bottom v-model="showQNumTooltip">
				        <v-btn slot="activator" @click="showQNumTooltip = !showQNumTooltip" icon small> <v-icon color="light-blue darken-1">info</v-icon></v-btn>
				        <span>     ,       .    ,   ,    ,          ,    .</span>
		        	</v-tooltip>
		          <v-text-field
		            type="number"
		            :rules="[rules.q_number_min, rules.q_number_max]"
		            v-model="questions_number"
		            required
		            solo
		          ></v-text-field>
		        </v-flex>

	     		<v-flex xs12 sm6>
  					<v-label> </v-label>
					<v-tooltip bottom v-model="showAuthorTooltip">
				        <v-btn slot="activator" @click="showAuthorTooltip = !showAuthorTooltip" icon small> <v-icon color="light-blue darken-1">info</v-icon></v-btn>
				        <span>,   </span>
		        	</v-tooltip>
		            <v-text-field
		              v-model="author"
		              required
		              :rules="[rules.required, rules.author]"
		              solo
		            ></v-text-field>
		        </v-flex>

		        <v-flex xs12 sm6>
  					<v-label> </v-label>
					<v-tooltip bottom v-model="showAttemptsTooltip">
				        <v-btn slot="activator" @click="showAttemptsTooltip = !showAttemptsTooltip" icon small> <v-icon color="light-blue darken-1">info</v-icon></v-btn>
				        <span> .    -  .</span>
		        	</v-tooltip>
		          <v-text-field
		            type="number"
		            :rules="[rules.attempts]"
		            v-model="attempts"
		            required
		            solo
		          ></v-text-field>
		        </v-flex>

		          <v-flex xs12 sm6 xl3>
		          	<v-label>  </v-label>
					<v-tooltip bottom v-model="showImageTooltip">
				        <v-btn slot="activator" @click="showImageTooltip = !showImageTooltip" icon small> <v-icon color="light-blue darken-1">info</v-icon></v-btn>
				        <span> ,      </span>
		        	</v-tooltip>
        			<v-layout align-center>
			          	<v-checkbox v-model="enabledImage" hide-details class="shrink mr-2" ></v-checkbox>
			            <file-input 
			            class="fileBtn"
	                    accept="image/*"
	                    ref="fileInput"
                        @input="getUploadedImage"
                        :checked="enabledImage"
			            :dis="!enabledImage"
			            :label="imageLoadText"
			            ></file-input>
			        </v-layout>
			      </v-flex>

				  <v-flex xs12>
				      <h4 class="title" d-block>   ( ):</h4>
				  </v-flex>

		        <v-flex xs12 sm4>
  					<v-label></v-label>
					<v-tooltip bottom v-model="showPerfectTooltip">
				        <v-btn slot="activator" @click="showPerfectTooltip = !showPerfectTooltip" icon small> <v-icon color="light-blue darken-1">info</v-icon></v-btn>
				        <span> ,    ''</span>
		        	</v-tooltip>
		          <v-text-field
		            type="number"
		            :rules="[rules.percents, rules.perfect]"
		            v-model="perfect_mark"
		            solo
		            required
		          ></v-text-field>
		        </v-flex>

		        <v-flex xs12 sm4>
  					<v-label></v-label>
					<v-tooltip bottom v-model="showGoodTooltip">
				        <v-btn slot="activator" @click="showGoodTooltip = !showGoodTooltip" icon small> <v-icon color="light-blue darken-1">info</v-icon></v-btn>
				        <span> ,    ''</span>
		        	</v-tooltip>
		          <v-text-field
		            type="number"
		            :rules="[rules.percents, rules.good]"
		            v-model="good_mark"
		            solo
		            required
		          ></v-text-field>
		        </v-flex>

		        <v-flex xs12 sm4>
  					<v-label></v-label>
					<v-tooltip bottom v-model="showSatisTooltip">
				        <v-btn slot="activator" @click="showSatisTooltip = !showSatisTooltip" icon small> <v-icon color="light-blue darken-1">info</v-icon></v-btn>
				        <span> ,    ''</span>
		        	</v-tooltip>
		          <v-text-field
		            type="number"
		            v-model="satisfactory_mark"
		            :rules="[rules.percents, rules.satisfactory]"
		            solo
		            required
		          ></v-text-field>
		        </v-flex>	

		        <v-flex xs12 sm6>
		        	<v-label>    ''</v-label>
        			<v-layout align-center>
			          	<v-checkbox v-model="enabledAudioPerfect" hide-details class="shrink mr-2"></v-checkbox>
			            <file-input 
			            class="fileBtn"
	                    accept="audio/*"
	                    ref="fileInput"
                        @input="getUploadedAudio($event, 0)"
			            :dis="!enabledAudioPerfect"
			            :checked="enabledAudioPerfect"
			            :label="audioLoadTextPerfect"
			            ></file-input>
			        </v-layout>
			      </v-flex>

			      <v-flex xs12 sm6>
		        	<v-label>    ''</v-label>
        			<v-layout align-center>
			          	<v-checkbox v-model="enabledAudioGood" hide-details class="shrink mr-2"></v-checkbox>
			            <file-input 
			            class="fileBtn"
	                    accept="audio/*"
	                    ref="fileInput"
                        @input="getUploadedAudio($event, 1)"
			            :dis="!enabledAudioGood"
			            :checked="enabledAudioGood"
			            :label="audioLoadTextGood"
			            ></file-input>
			        </v-layout>
			      </v-flex>

			      <v-flex xs12 sm6>
		        	<v-label>    ''</v-label>
        			<v-layout align-center>
			          	<v-checkbox v-model="enabledAudioSatisfactory" hide-details class="shrink mr-2"></v-checkbox>
			            <file-input 
			            class="fileBtn"
	                    accept="audio/*"
	                    ref="fileInput"
                        @input="getUploadedAudio($event, 2)"
			            :dis="!enabledAudioSatisfactory"
			            :checked="enabledAudioSatisfactory"
			            :label="audioLoadTextSatisfactory"
			            ></file-input>
			        </v-layout>
			      </v-flex>

			      <v-flex xs12 sm6>
		        	<v-label>    ''</v-label>
        			<v-layout align-center>
			          	<v-checkbox v-model="enabledAudioBad" hide-details class="shrink mr-2"></v-checkbox>
			            <file-input 
			            class="fileBtn"
	                    accept="audio/*"
	                    ref="fileInput"
                        @input="getUploadedAudio($event, 3)"
			            :dis="!enabledAudioBad"
			            :checked="enabledAudioBad"
			            :label="audioLoadTextBad"
			            ></file-input>
			        </v-layout>
			      </v-flex>

			      <v-flex xs12>
		            <v-label>    (id   Youtube)</v-label>
		              <v-text-field
		                type="text"
		                v-model="video"
		                :rules="[rules.video]"
			            placeholder=", _5k9NMCQ088"
		                clearable
		                solo
		              ></v-text-field>
		            </v-flex>

				<v-flex xs12>
					<v-alert
			        :value="alert"
			        :type="successSet ? 'success' : 'error'"
			      	>
			        {{message}}

				    <v-btn v-if="successSet" to="/tests" flat>c  </v-btn>
				    <v-btn v-if="!successSet" @click.native="onSubmit()" flat>  </v-btn>
			      </v-alert>
		      </v-flex>	

				<v-flex xs12>
					<v-btn round color="success" @click.native="onSubmit()" dark large :loading="isSubmitting">
						  
					</v-btn>
				</v-flex>	        		        
			    
			</v-layout>
		</v-container>
	</v-form>
</template>

<script>
    import Axios from 'axios'
	import router from '@/router'
    import FileInput from '@/components/other/FileLoader'
    import AddedQuestion from '@/components/boxes/AddedQuestion'
	import Authentication from '@/components/pages/Authentication'
	import connection from '@/router/connection'

	const TestingSystemAPI = connection.server

    export default {
        components: { FileInput, AddedQuestion },

		data () {
		    return {
                image: '',
                imageLoadText: '  ',

			    enabledImage: false,
			    questions: [],
			    questionsChecks: [],
			    error : false,
			    successSet: false,
			    alert: false,
			    message: '',
			    checkTokenObserver: false,
			    isSubmitting: false,

			    showTestTooltip: false,
			    showTitleTooltip: false,
			    showSatisTooltip: false,
			    showGoodTooltip: false,
			    showPerfectTooltip: false,
			    showAttemptsTooltip: false,
			    showAuthorTooltip: false,
			    showQNumTooltip: false,
			    showLinkTooltip: false,
			    showDescriptionTooltip: false,
			    showImageTooltip: false,

			    title: '',
			    token: '',
			    attempts: '',
			    questions_number: '',
			    author: '',
			    description: '',
			    perfect_mark: 75,
			    good_mark: 50,
			    satisfactory_mark: 25,
			    max_percent: 100,
			    min_percent: 0,
        		maxAttempts: 100,
        		minAttempts: 0,
        		maxAuthorLen: 70,
        		minAuthorLen: 10,
        		minDescrLen: 50,
        		maxDescrLen: 2000,
        		minTitleLen: 8,
        		maxTitleLen: 100,
        		minTokenLen: 8,
        		maxTokenLen: 50,
        		videoLength: 11,
        		validToken: true,
        		audioPerfect: null,
        		audioGood: null,
        		audioSatisfactory: null,
        		audioBad:null,
        		audioLoadTextPerfect:'',
        		audioLoadTextGood:'',
        		audioLoadTextSatisfactory:'',
        		audioLoadTextBad:'',
        		enabledAudioBad:false,
        		enabledAudioSatisfactory:false,
        		enabledAudioGood:false,
        		enabledAudioPerfect:false,
        		video:'',

                rules: {
                	required: value => !!value || '  ',
                	percents: value => (value >= this.min_percent && value <= this.max_percent) || '   ' + this.min_percent + '  ' + this.max_percent,
                	perfect: value => (value > this.good_mark && value > this.satisfactory_mark) || '   ,    ""',
                	good: value => (value < this.perfect_mark && value > this.satisfactory_mark) || '      ""  ""',
                	satisfactory: value => (value < this.good_mark && value < this.perfect_mark) || '   ,    ""',
                	attempts: value => (value >= this.minAttempts && value <= this.maxAttempts) || '     '+this.minAttempts+'  '+this.maxAttempts,
                	author: value => (value.length >= this.minAuthorLen && value.length <= this.maxAuthorLen) || '       '+this.minAuthorLen +'  '+this.maxAuthorLen + ' ',
                	q_number_min: val => val > 0 || '    0',
                	q_number_max: val => val <= this.questionsChecks.length || '   ,   ,  ',
                	description: val => (val.length >= this.minDescrLen && val.length <= this.maxDescrLen) || '       '+this.minDescrLen+'  '+this.maxDescrLen + ' ',
                	title: val => (val.length >= this.minTitleLen && val.length <= this.maxTitleLen) || '       '+this.minTitleLen+'  '+this.maxTitleLen + ' ',
                	tokenValid: async (val) => await this.getTokenAnswer() || '',
                	token: val => (val.length >= this.minTokenLen && val.length <= this.maxTokenLen) || '       '+this.minTokenLen+'  '+this.maxTokenLen + ' ',
                	video: str => (str.length == 0 || str.length == this.videoLength) || '    Youtube  '+ this.videoLength + ' ',
                },

		    }
		},
		methods: {
			async getTokenAnswer(){
				var ans = false
				await this.checkToken()
				.then(
					res=>{
						ans = res
						if (ans == 'true')
							this.validToken = true
						else this.validToken = false
					}, 
					err => {
						console.log(err)
					}
				)
				return this.validToken
			},
			getAddedQuestions() {
				Axios.get(`${TestingSystemAPI}/api/questionslist/`, {
		          headers: { 'Authorization': Authentication.getAuthenticationHeader(this) },
		          params: {}
		        }).then(({data}) => {
		          this.questions = data
		          this.questions.reverse()
		          for (var i = 0; i < this.questions.length; ++i)
		          	this.questions[i].show = false
		        }).catch(error => {
		          this.error = true

		          console.log(error)
		        })
			},
            getUploadedAudio(e, val) {
            	if (val == 0)
                	this.audioPerfect = e
                else if (val == 1)
                	this.audioGood = e
                else if (val == 2)
                	this.audioSatisfactory = e
                else
                	this.audioBad = e
            },
            getUploadedImage(e) {
                this.image = e
            },
            goBack() {
            	router.push('/tests')
            },
            onSubmit() {
	        	if (!this.$refs.addTform.validate() || !this.validToken)
	        	{
               		this.successSet = false
                    this.alert = true
                    this.message = '     .'
	        		return
	        	}

	        	if (this.successSet)
	        		return
	        	 this.isSubmitting = true
                 let formData = new FormData()

                 formData.set('name', this.title)
                 formData.set('token', this.token)
                 formData.set('attempts', this.attempts)
                 formData.set('questions_number', this.questions_number)
                 formData.set('author', this.author)
                 formData.set('description', this.description)
                 formData.set('image', this.image)
                 formData.set('perfect_mark', this.perfect_mark)
                 formData.set('good_mark', this.good_mark)
                 formData.set('satisfactory_mark', this.satisfactory_mark)
                 formData.set('perfect_audio', this.audioPerfect)
                 formData.set('good_audio', this.audioGood)
                 formData.set('satisfactory_audio', this.audioSatisfactory) 
                 formData.set('bad_audio', this.audioBad)
                 formData.set('video', this.video)
                 for (var i = 0; i < this.questionsChecks.length; i++)
                 	formData.append('questions', this.questionsChecks[i])

				let object = {};
				formData.forEach(function(value, key){
				    object[key] = value;
				});
				let json = JSON.stringify(object);
                 console.log(json)
                 console.log(formData)

                Axios.post(`${TestingSystemAPI}/api/courses/`, formData, {
			        headers: { 'Authorization': Authentication.getAuthenticationHeader(this) },
			        params: {}
		        })
               .then((response) => {
               		this.successSet = true
                    this.alert = true
                    this.message = '  .'
        			this.isSubmitting = false
                    setTimeout(this.goBack, 1200)
                })
               .catch((error) => {
               		this.successSet = false
                    this.alert = true
                    this.message = '   .    .'
        			this.isSubmitting = false
                })
            },
            textToTranslit(text) {
            	var transl = []
				transl['']='a'
				transl['']='b'
				transl['']='v'
				transl['']='g'
				transl['']='d'
				transl['']='e'
				transl['']='yo'
				transl['']='zh'
				transl['']='z'
				transl['']='i'
				transl['']='j'
				transl['']='k'
				transl['']='l'
				transl['']='m'
				transl['']='n'
				transl['']='o'
				transl['']='p'
				transl['']='r'
				transl['']='s'
				transl['']='t'
				transl['']='u'
				transl['']='f'
				transl['']='kh'
				transl['']='c'
				transl['']='ch'
				transl['']='sh'
				transl['']='shch'
				transl['']=''
				transl['']='i'
				transl['']=''
				transl['']='e'
				transl['']='yu'
				transl['']='ya'
				transl[' ']='_'
				transl['']='a'
				transl['']='b'
				transl['']='v'
				transl['']='g'
				transl['']='d'
				transl['']='e'
				transl['']='yo'
				transl['']='zh'
				transl['']='z'
				transl['']='i'
				transl['']='j'
				transl['']='k'
				transl['']='l'
				transl['']='m'
				transl['']='n'
				transl['']='o'
				transl['']='p'
				transl['']='r'
				transl['']='s'
				transl['']='t'
				transl['']='u'
				transl['']='f'
				transl['']='kh'
				transl['']='c'
				transl['']='ch'
				transl['']='sh'
				transl['']='shch'
				transl['']=''
				transl['']='i'
				transl['']=''
				transl['']='e'
				transl['']='yu'
				transl['']='ya'
				transl['/']=''
				transl['\\']=''


				var result=''
			    for (var i=0; i < text.length; i++) {
			        if(transl[text[i]]!=undefined)  
			        	result += transl[text[i]]
			        else 
			        	result += text[i]
    			}
			    return result
            },
			toggleShow(id) {
				for (var i = 0; i < this.questions.length; ++i)
					if (this.questions[i].id == id)
					{
						this.questions[i].show = !this.questions[i].show
						this.questions.push(null)
						this.questions.pop()
						return
					}
			},
			async checkToken() {
				const data = { 'token' : this.token}
				const req = await Axios.post(`${TestingSystemAPI}/api/token-check/`, data, {
			          headers: { 'Authorization': Authentication.getAuthenticationHeader(this) },
			          params: {}
			    })
                .then((response) => {
					this.checkTokenObserver = false;
                	return response.data.avaliable
                })
                .catch((error) => {
					this.checkTokenObserver = false;
					return false
                })
                return req
			}
		},
		mounted(){
			this.getAddedQuestions()
		},
		watch: {
			title: function(val) {
				this.token = this.textToTranslit(val)
			},
			satisfactory_mark: function(val) {
				console.log('s: '+val + ' g: ' + this.good_mark + ' p: ' + this.perfect_mark)
			},
			good_mark: function(val) {
				console.log('s: '+this.satisfactory_mark + ' g: ' + val + ' p: ' + this.perfect_mark)
			},
			perfect_mark: function(val) {
				console.log('s: '+ this.satisfactory_mark + ' g: ' + this.good_mark + ' p: ' + val)
			},
			questionsChecks: function(val) {
				this.questions_number = val.length
			}
		}
    }
	
</script>

<style>
	.content-wrapper{
		overflow-y: auto;
		border-radius: 4px;
		border: 1px solid #919191;
		height: 400px;
		margin-bottom: 10px;
		background: #EBEBEB;
		padding: 15px;
	}
</style>


